.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.title{
  text-align: left;
}
.search {
  display: flex;
}
.search .SearchItems{
  display: flex;
  margin-right: 10px;
}
.search .SearchItems button{
  background-color: #4dffdb;
}
.search .Sort .dropdown button{
  background-color: white;
  color: black;
}
.ClosedItems .panel-info .panel-body {
  display: flex;
}
.ClosedItems .panel-info .panel-body .ButtonCancel{
  background-color: white;
  color: black;
}